.notifications-container {
	width: 80%;
	margin: auto;
	padding-top: 70px;
	margin-bottom: 5em;
}

.notifications-container h1 {
	color: #085a5a !important;
	text-shadow: 1px 1px 2px #888;
	text-align: center;
	font-family: "Bree Serif";
	/*border-bottom: 1px solid #085a5a;*/
	margin: 0 25% 0.25em 25%;
}

.notifications-container-inner {
	border: 5px dashed #ccc;
	border-radius: 20px;
	padding: 5px 15px;
	padding-bottom: 40px;
}

.notifications-none {
	text-align: center;
	font-weight: bold;
	padding-top: 20px;
}



