/* DASHBOARD STYLES */
.dashboard-container {
  padding-top: 70px;
  height: 100%;
  background-color: #85dcdc;
  /*background-image: url('https://triumphmodular.com/wp-content/uploads/2015/05/project-newton-oakhill-interior.jpg');*/
  background-size: cover;
  margin-bottom: 5em;
}
.dashboard-container h1 {
  color: #085a5a !important;
  text-shadow: 1px 1px 2px #888;
  text-align: center;
  font-family: "Bree Serif";
  margin: 0 25% 0.25em 25%;
}
.dashboard-menu-grid h3 {
  font-family: "Bree Serif";
}
.dashboard-menu {
  display: block;
  text-decoration: none;
  margin: 0 auto;
  width: 80%;
}
.dashboard-menu-inner {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  width: 99.4%;
  height: 300px;
}
.student-dashboard-menu-inner {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 99.4%;
  height: 300px;
}

.dashboard-menu-item {
  text-decoration: none;
  height: 260px; /*change later */
  width: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 20px;
  display: grid;
  border: 4px solid;
}
.dashboard-menu-grid {
  display: grid;
  justify-items: center;
  grid-template-rows: 1.5/3 1.5/3;
}
.db1:hover {
  background-color: rgba(220, 43, 43, 0.2);
}
.db2:hover {
  background-color: rgba(26, 132, 132, 0.3);
}
.db3:hover {
  background-color: rgba(150, 205, 40, 0.3);
}
#dashboard-item-higher {
  grid-row-start: 1;
  grid-row-end: 2;
}
#dashboard-item-lower {
  grid-row-start: 2;
  grid-row-end: 3;
}
#dashboard-menu-item1 {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 1;
  border-color: #dc2b2b;
  color: #dc2b2b; /*dont think this taking affect*/
}
#dashboard-menu-item1 i {
  color: #dc2b2b; /*dont think this taking affect*/
}
#dashboard-menu-item2 {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 1;
  border-color: #1a8484;
  color: #1a8484; /*dont think this taking affect*/
}
#dashboard-menu-item2 i {
  color: #1a8484; /*dont think this taking affect*/
}
#dashboard-menu-item3 {
  grid-column-start: 3;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 1;
  border-color: #96cd28;
  color: #96cd28; /*dont think this taking affect*/
}
#dashboard-menu-item3 i {
  color: #96cd28; /*dont think this taking affect*/
}



